import React, { useEffect, useState } from "react";
import { Modal } from "antd";
import "./myModal.scss";
import Num2persian from "num2persian";


export default function MyModal({ actualPrice, fakePrice, modalState, onCloseModal }) {
  const [modal, setmodal] = useState(false);
  
  useEffect(() => {
    setmodal(modalState);
  }, [modalState])
  
  if(!actualPrice || !fakePrice) return;

  const handleClose = () => {
    setmodal(false);
    onCloseModal();
  }


  return (
    <>
      <Modal
        className="myModal"
        footer={null}
        closable={true}
        okText=""
        title=" "
        centered
        open={modal}
        onOk={handleClose}
        onCancel={handleClose}
      >
        <div className="m-3">
          <div className="alert alert-success">
            <h5>   :</h5>
            <p className="mt-2"><span>{ parseInt(actualPrice) }</span> </p>
            <p>{ Num2persian(parseInt(actualPrice)) }  </p>
          </div>
          

          <hr />


          <div className="alert alert-warning">
            <h5>      :</h5>
            <p className="mt-2"><span>{ parseInt(fakePrice.finalPrice1) }</span> </p>
            <p>{ Num2persian(parseInt(fakePrice.finalPrice1)) }  </p>
            {/* <hr />
            <p className="mt-2"><span>{ parseInt(fakePrice.finalPrice2) }</span> </p>
            <p>{ Num2persian(parseInt(fakePrice.finalPrice2)) }  </p> */}
          </div>
        </div>
      </Modal>
    </>
  );
}