import React, { useState } from "react";
import { Form, Input, Button, message, Checkbox } from "antd";
import "./inputs.scss";
import MyModal from "./MyModal";
import { actualPrice, fakePrice } from "../logic/calculations";
import Num2persian from "num2persian";

export default function Inputs() {
  const [messageApi, contextHolder] = message.useMessage();
  const [err, setErr] = useState(null);
  const [modalState, setModalState] = useState(false);
  const [actualP, setActualP] = useState(null);
  const [fakeP, setFakeP] = useState(null);

  const onFinish = (values) => {
    let Ap = actualPrice(
      parseFloat(values["gold-weight"]), 
      parseFloat(values["gold-price"]), 
      parseFloat(values["gold-emolument"]), 
      parseFloat(values["gold-benefit"]),
      values.tax
    );
    let Fp = fakePrice(
      parseFloat(values["gold-weight"]), 
      parseFloat(values["gold-price"]), 
      parseFloat(values["gold-emolument"]), 
      parseFloat(values["gold-benefit"]),
      values.tax
    );
    setActualP(Ap);
    setFakeP(Fp);
    setErr(null);
    setModalState(true);
  };
  
  const onFinishFailed = (errorInfo) => {
    messageApi.open({
      type: 'error',
      content: '     ',
      duration: 2
    });
    setErr(errorInfo);
    setModalState(false);
  };

  const onCloseModal = () => {
    setModalState(false);
  }

  const onWeightBlur = (e) => {
    let w = e.target.value;
    if(parseFloat(w) && (!(parseFloat(w) > 1_000_000) && !(parseFloat(w) < 0))) {
      messageApi.open({
        type: 'success',
        content: Num2persian(parseFloat(e.target.value)) + " ",
        duration: 2
      });
    }
  }

  const onPriceBlur = (e) => {
    let p = e.target.value;
    if(parseFloat(p) && (!(parseFloat(p) > 10_000_000) && !(parseFloat(p) < 0))) {
      messageApi.open({
        type: 'success',
        content: Num2persian(parseFloat(e.target.value)) + " ",
        duration: 2
      });
    }
  }

  const onEmolumentBlur = (e) => {
    let em = e.target.value;
    if(parseFloat(em) && (!(parseFloat(em) > 100) && !(parseFloat(em) < 0))) {
      messageApi.open({
        type: 'success',
        content: Num2persian(parseFloat(e.target.value)) + " ",
        duration: 2
      });
    }
  }

  const onBenefitBlur = e => {
    let b = e.target.value;
    if(parseFloat(b) && (!(parseFloat(b) > 100) && !(parseFloat(b) < 0))) {
      messageApi.open({
        type: 'success',
        content: Num2persian(parseFloat(e.target.value)) + " ",
        duration: 2
      });
    }
  }

  return (
    <div>
      {contextHolder}
      <Form
        className="form"
        name="basic"
        labelCol={{
          span: 8,
        }}
        wrapperCol={{
          span: 16,
        }}
        style={{
          maxWidth: 600,
        }}
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label=" "
          name="gold-weight"
          rules={[
            {
              required: true,
              message: '  ',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if(!value) return Promise.resolve();
                if(!parseFloat(value)) {
                  return Promise.reject(new Error("    "))
                }
                if(value > 1_000_000) {
                  return Promise.reject(new Error("     !"));
                }
                if(value < 0) {
                  return Promise.reject(new Error("     :)"));
                }
                return Promise.resolve();
              }
            })
          ]}
        >
          <Input placeholder="  (: )" onBlur={onWeightBlur}/>
        </Form.Item>
          <br />
        <Form.Item
          label=" "
          name="gold-price"
          rules={[
            {
              required: true,
              message: '  ',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if(!value) return Promise.resolve();
                if(!parseFloat(value)) {
                  return Promise.reject(new Error("    "))
                }
                if(value > 10_000_000) {
                  return Promise.reject(new Error("      !"));
                }
                if(value < 0) {
                  return Promise.reject(new Error("  !"));
                }
                return Promise.resolve();
              }
            })
          ]}
        >
          <Input placeholder=" " onBlur={onPriceBlur}/>
        </Form.Item>
      
          <br />
        <Form.Item
          label=" "
          name="gold-emolument"
          rules={[
            {
              required: true,
              message: '  !',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if(!value) return Promise.resolve();
                if(!parseFloat(value)) {
                  return Promise.reject(new Error("    "))
                }
                if(value > 100) {
                  return Promise.reject(new Error("    :)"));
                }
                if(value < 0) {
                  return Promise.reject(new Error("  !    "));
                }
                return Promise.resolve();
              }
            })
          ]}
        >
          <Input placeholder="  (: )" onBlur={onEmolumentBlur}/>
        </Form.Item>
          <br />
        <Form.Item
          label=" "
          name="gold-benefit"
          rules={[
            {
              required: true,
              message: '  ',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if(!value) return Promise.resolve();
                if(!parseFloat(value)) {
                  return Promise.reject(new Error("    "))
                }
                if(value > 100) {
                  return Promise.reject(new Error("    :)"));
                }
                if(value < 0) {
                  return Promise.reject(new Error("  !"));
                }
                return Promise.resolve();
              }
            })
          ]}
        >
          <Input placeholder="  (: )" onBlur={onBenefitBlur}/>
        </Form.Item>
        <br />
        <Form.Item
          className="d-flex justify-content-right"
          name="tax"
          valuePropName="checked"
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
        <Checkbox>   </Checkbox>
        </Form.Item>
        <br />
        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
        <MyModal
          actualPrice={actualP}
          fakePrice={fakeP}
          modalState={modalState}
          onCloseModal={onCloseModal} />
      
        <Button
          className="d-flex jusitfy-content-center"
          type="primary"
          htmlType="submit" >
        
        </Button>
        </Form.Item>
      </Form>
    </div>
  );
}

